import Sidebar from "@/components/sidebar";
import { Select } from "@nextui-org/select";


export default function Patch_Management() {

  const months = [
    {key: "january", label: "January"},
    {key: "february", label: "February"},
    {key: "march", label: "March"},
    {key: "april", label: "April"},
    {key: "may", label: "May"},
  ];
  return (
    <>
       <Sidebar/>   
       <aside id="patch_management" className=" leftSide fixed top-5 right-2 z-40 w-4/5 bg-white cust_h-56 transition-transform -translate-x-full sm:translate-x-0 p-8" aria-label="patch_management">
        <div className="grid grid-rows-3 grid-flow-col gap-4">
          <div className="row-span-3"><h1 className="text-purple text-3xl font-semibold border-heading">Patch Management Report</h1></div>
          <div className="row-span-2 col-span-1">
          <div className="flex w-full flex-wrap md:flex-nowrap gap-4 month_wrap">
              <Select
                label="Select Month" 
                className="max-w-xs" 
              >
                {months.map((month) => (
                  <SelectItem key={month.key}>
                    {month.label}
                  </SelectItem>
                ))} 
              </Select>
              </div>
          </div>
        </div>
       </aside>
    </>
  );
}
